.vocabulary-table-container {
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 100vh;
}

.table-wrapper {
    display: flex;
    max-width: 800px;
    min-width: 200px;
    width: 100%;
    align-items: center;
    flex-direction: column;
    margin: 10px;
    overflow-y: auto;
    border: 2px solid #ddd;
    border-radius: 20px;
    min-height: 300px;
}

.table-wrapper::-webkit-scrollbar {
    width: 12px; /* Adjust width of the scrollbar */
    background-color: transparent;
}

.table-wrapper::-webkit-scrollbar-thumb {
    background-color: #c1c1c1;
    border-radius: 10px;
}

.table-wrapper::-webkit-scrollbar-thumb:hover {
    background-color: #909090;
}

.table-wrapper::-webkit-scrollbar-track {
    background-color: transparent;
    border-radius: 10px;
}

/* For Firefox */
.table-wrapper {
    scrollbar-width: thin;
    scrollbar-color: #c1c1c1 #F5F5F5;
}

.vocabulary-table {
    border-collapse: collapse;
    width: 100%;
    padding: 20px;
}

.remove-word-btn {
    border: 2px solid transparent;
    outline: none;
    font-weight: 580;
    transition: 0.4s;
    width: 100%;
    height: 100%;
    padding: 10px;
    border-radius: 10px;
    letter-spacing: 1px;
    box-shadow: none;
    background-color: transparent;
    color: #b46969;
}

.remove-word-btn:hover {
    cursor: pointer;
    border: 2px solid #b46969;
}

.remove-word-td {
    border: 1px solid #ddd;
    padding: 6px;
}

.vocabulary-table th,
.content-td {
    width: 33%;
    padding: 10px;
    text-align: center;
    border: 1px solid #ddd;
}

.vocabulary-table th {
    border-top: 0;
    background-color: #f0f0f0;
    position: sticky;
    top: 0;
}

.add-vocabulary-button-wrapper {
    display: flex;
    align-items: center;
    width: 100%;
    max-width: 800px;
    min-width: 200px;
    margin: 10px;
}

.add-vocabulary-button {
    width: 100%;
    max-width: 800px;
    height: 50px;
    border: 3px solid #1cb0f6;
    box-shadow: 0 5px 0 #24abe1;
    background-color: #ddf4ff;
    color: #1cb0f6;
    font-weight: 700;
    font-size: 1rem;
    outline: none;
    letter-spacing: 1px;
    border-radius: 20px;
}

.add-vocabulary-button:active {
    box-shadow: none;
    transform: translateY(5px);
}

.add-vocabulary-button:hover {
    cursor: pointer;
}